@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.home,
.conocimientos,
.proyectos,
.contacto,
.error {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
}

.proyectos,.conocimientos{
  flex-direction: column;
}

.experiencia{
  flex-direction: column;
}

@media only screen and (max-width: 900px) {

  .conocimientos, .proyectos {
      height: 100%;
  }

  .contacto {
    flex-wrap: wrap;
  }
}
.navbar{
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu{
    background-color: #090e2e;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active{
    left: 0;
    transition: 350ms;
}

.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover{
    background-color: #1a83ff;
}

.nav-menu-items{
    width: 100%;
}

.navbar-toggle{
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span{
    margin-left: 16px;
}
.profile_container{
    display: grid;
    grid-template-columns: 1fr 1fr;

}

.cont_perfil{
    text-align: center;
}

.cont_perfil img{
    width: 200px;
    border-radius: 50%;
}

.cont_perfil h3{
    font-size: 25px;
    margin: auto;
}

.cont_perfil h4{
    font-size: 21px;
}

.cont_personal{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;
    margin: 15px auto;
    text-align: center;
}

.cont_personal p{
    width: 80%;
    margin-top: 15px;
}

.cont_personal img{
    width: 140px;
}

.cont_cursos{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    text-align: center;
    padding: 10px;
}

.cont_cursos div{
    margin: 0 25px;
    display: flex;
    flex-direction: column;
}

.cont_cursos h5{
    font-size: 16px;
    margin: 0;
}

.cont_cursos img{
    width: 75px;
    margin: auto;
}

.cont_cursos p{
    font-size: 24px;
    margin: 10px 0;
}

.stack_container{
    margin-top: 32px;
}

.stackdev h3{
    text-align: center;
    margin: 12px 0;
}

@media only screen and (max-width: 600px) {

    .profile_container{
        grid-template-columns: 1fr;
    }
}

@media only screen and (max-width: 400px) {

    .cv_container{
        height: 100%;
    }

    .cont_perfil h3{
        width: 60%;
    }
}
.webscontainer{
    display: grid;
    grid-gap: 40px;
    gap: 40px;
    text-align: center;
    grid-template-columns: repeat( auto-fit, minmax(350px, 1fr) );
    justify-content: center;
}

.webscontainer div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 10px 50px;
}

.webscontainer div button{
    padding: 10px;
    background-color: rgb(191, 190, 255);
    border-radius: 12px;
    cursor: pointer;
}

.webscontainer div p{
    margin: 5px 0;
}

.webscontainer div button:hover{
    background-color: rgb(118, 80, 255);
}

.webscontainer div button:active{
    background-color: rgb(66, 24, 214);
    box-shadow: 0 0 8px black;
}
h4{
    font-size: 1.5rem;
    margin-bottom: 14px;
}

.buttons {
    background-color: rgb(138, 255, 255);
    padding: 10px;
    border-radius: 8px;
}

.buttons button{
    margin: 0 15px;
    border:none;
    padding: 5px 10px;
    border-radius: 7px;
}

.lista{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    list-style-type: none;
}

.lista li{
    height: 180px;
    margin: 10px 40px;
    display: flex;
    flex-direction: column;
}

.lista img{
    width: 100px;
    margin: auto;
}

.lista p {
    font-size: 18px;
    text-align: center;
    margin-bottom: 15px;
}




.cont_formulario{
    width: 50%;
    margin: auto;
}

.formulario{
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 232, 232);
    padding: 20px;
    border-radius: 25px;
}

.formulario label{
    margin-bottom: 2px;
}

.formulario input{
    margin-bottom: 12px;
    border-radius: 6px;
    padding: 3px 5px;
    background-color: rgb(228, 226, 255);
    border: none;
}


.cont_formulario h4{
    text-align: center;
    margin-bottom: 12px;
}

.formulario textarea{
    margin: 10px 0 12px 0;
    border-radius: 6px;
    padding: 3px 5px;
    background-color: rgb(228, 226, 255);
}

#textarea{
    resize: none;
}

.formulario button{
    padding: 4px 0;
}

.formulario button:active{
    background-color: cornflowerblue;
}

@media only screen and (max-width: 900px) {

    .cont_formulario {
        height: 100%;
        width: 90%;
    }
  }
.redescontainer{
    margin: auto;
}

.redescontainer div{
    display: flex;
    align-items: center;
    margin: 20px 0;
}

.redescontainer div img{
    width: 20px;
    margin: 10px;
}

.redescontainer div a:visited{
    color: blue;
}
